import * as fb from 'firebase'

class Services {
  constructor({name, price, description}, ownerId, id = null) {
    this.name = name
    this.price = price
    this.description = description
    this.ownerId = ownerId
    this.id = id
  }
}

export default {
  state: {
    services: []
  },
  mutations: {
    ADD_SERVICES(state, payload) {
      state.services.push(payload)
    },
    DELETE_SERVICES(state, payload) {
      state.services = state.services.filter(a => a.id !== payload)
    },
    LOAD_SERVICES (state, payload) {
      state.services = payload
    },
    UPDATE_SERVICES (state, {title, name, price, description, id} ) {
      const s = state.services.find(a => a.id === id)
      s.price = price
      s.title = title
      s.name = name
      s.description = description
    }
  },
  actions: {
    async addServices({commit, getters}, payload) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      try {
        const newServices =  new Services(payload, getters.getUserInfo.uid)
        const responseDB = await fb.database().ref('services').push(newServices)
        commit('SET_LOADING', false)
        commit('ADD_SERVICES', {
          ...newServices,
          id: responseDB.key
        })
      } catch (err) {
        commit('SET_ERROR', err.message)
        commit('SET_LOADING', false)
      }
    },
    async fetchServices({commit}) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      const resultServ = []
      try {
        const valDB = await fb.database().ref('services').once('value')
        const val = valDB.val()
        if (val !== null) {
          Object.keys(val).forEach(key => {
            const serv = val[key]
            resultServ.push(
              new Services(serv, serv.ownerId, key)
            )
          })
          commit('LOAD_SERVICES', resultServ)
        }
        commit('SET_LOADING', false)
      } catch(err) {
        commit('SET_ERROR', err.message)
        commit('SET_LOADING', false)
        throw err
      }
    },
    async editServices({commit}, {name, price, description, id}) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      try {
        await fb.database().ref('services').child(id).update({ name, price, description })
        commit('UPDATE_SERVICES', { name, price, description, id })
        commit('SET_LOADING', false)
      } catch (err) {
        commit('SET_ERROR', err.message)
        commit('SET_LOADING', false)
        throw err
      }
    },
    async deleteServices({commit}, payload) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)

      try {
        await fb.database().ref('services').child(payload).remove()
        commit('DELETE_SERVICES', payload)
        commit('SET_LOADING', false)
      } catch (err) {
        commit('SET_ERROR', err.message)
        commit('SET_LOADING', false)
        throw err
      }
 
    },
  },
  getters: {
    getServicesInfo(state, getters) {
      return state.services.filter(serv => serv.ownerId === getters.getUserInfo.uid)
    }
  } 
}