import * as fb from 'firebase'

class Clients {
  constructor({name, phone, email}, ownerId, id = null) {
    this.name = name
    this.phone = phone
    this.email = email
    this.ownerId = ownerId
    this.id = id
  }
}

export default {
  state: {
    clients: []
  },
  mutations: {
    ADD_CLIENT(state, payload) {
      state.clients.push(payload)
    },
    LOAD_CLIENTS(state, payload) {
      state.clients = payload
    }
  },
  actions: {
    async addClient({commit, getters}, payload) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      try {
        const newClients = new Clients(payload, getters.getUserInfo.uid)
        const responseDB = await fb.database().ref('uclients').push(newClients)
        commit('SET_LOADING', false)
        commit('ADD_CLIENT', {
          ...newClients,
          id: responseDB.key
        })
      } catch(err) {
        commit('SET_LOADING', false)
        commit('SET_ERROR', err.message)
      }
    },
    async fetchClients({commit}) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      const resultClients = []
      try {
        const valDB = await fb.database().ref('uclients').once('value')
        const val = valDB.val()
        if (val !== null) {
          Object.keys(val).forEach(key => {
            const client = val[key]
            resultClients.push(
              new Clients(client, client.ownerId, key)
            )
          })
          commit('LOAD_CLIENTS', resultClients)
        }
        commit('SET_LOADING', false)
      } catch(err) {
        commit('SET_ERROR', err.message)
        commit('SET_LOADING', false)
        throw err
      }
    }
  },
  getters: {
    getClientsList(state) {
      return state.clients
    }
  }
}