import * as fb from 'firebase'
class User {
  constructor(uid, email) {
    this.uid = uid
    this.email = email
  }
}

export default {
  state: {
    user: null
  },
  mutations: {
    SET_USER (state, payload) {
      state.user = payload
    }
  },
  actions: {
    async registrationUser ({commit}, {email, password}) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      try {
        const response = await fb.auth().createUserWithEmailAndPassword(email, password)
        commit('SET_USER', new User(response.user.uid, response.user.email))
        commit('SET_LOADING', false)
      } catch (err) {
        commit('SET_LOADING', true)
        commit('SET_ERROR', err.message)
        throw err
      }
    },
    async loginUser ({commit}, {email, password}) {
      commit('CLEAR_ERROR')
      commit('SET_LOADING', true)
      try {
        const response = await fb.auth().signInWithEmailAndPassword(email, password)
        commit('SET_USER', new User(response.user.uid, response.user.email))
        commit('SET_LOADING', false)
      } catch (err) {
        commit('SET_LOADING', false)
        commit('SET_ERROR', err.message)
        throw err
      }
    },
    autoLoginUser ({commit}, payload) {
      commit('SET_USER', new User(payload.uid, payload.email))
    },
    logoutUser ({commit}) {
      fb.auth().signOut()
      commit('SET_USER', null)
    }
  },
  getters: {
    getUserInfo (state) {
      return state.user
    }
  }
}