export default {
  state: {
    loading: false,
    error: null
  },
  mutations: {
    SET_LOADING (state, paylaod) {
      state.loading = paylaod
    },
    SET_ERROR (state, paylaod) {
      state.error = paylaod
    },
    CLEAR_ERROR (state) {
      state.error = null
    }
  },
  actions: {
    setLoading ({commit}, payload) {
      commit('SET_LOADING', payload)
    },
    setError ({commit}, payload) {
      commit('SET_ERROR', payload)
    },
    clearError ({commit}) {
      commit('CLEAR_ERROR')
    }
  },
  getters: {
    loading (state) {
      return state.loading
    },
    error (state) {
      return state.error
    }
  }
}