import Vue from 'vue'
import Vuex from 'vuex'
import mainInfo from './mainInfo'
import services from './services'
import clients from './clients'
import users from './users'
import shared from './shared'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    shared,
    users,
    mainInfo,
    services,
    clients
  }
})