export default {
  state: {
    info: {
      profit: 0,
      clients: 0,
      orders: 0,
      drawer: true,
      darkTheme: false
    }
  },
  mutations: {
    SET_DRAWER(state, payload) {
      state.info.drawer = payload
    },
    SWITCH_THEME(state, payload) {
      state.info.darkTheme = payload
    }
  },
  actions: {
    setDrawer({commit}, payload) {
      commit('SET_DRAWER', payload)
    },
    switchTheme({commit}, payload) {
      commit('SWITCH_THEME', payload)
    }
  },
  getters: {
    getAllInfo(state) {
      return state.info
    }
  }
}