import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import router from './plugins/router'
import store from './store'
import * as fb from 'firebase'

Vue.config.productionTip = true

fb.initializeApp({
  apiKey: "AIzaSyAWJm4ksACPQmA2Es7_vsENgMZbzomtYuA",
  authDomain: "lk-pflp.firebaseapp.com",
  databaseURL: "https://lk-pflp.firebaseio.com",
  projectId: "lk-pflp",
  storageBucket: "",
  messagingSenderId: "194171039592",
  appId: "1:194171039592:web:b222c75a24b89093"
}) 

const unsubscribe = fb.auth().onAuthStateChanged(user => {
  if (user) {
    store.dispatch('autoLoginUser', user)
  }
    new Vue({
      vuetify,
      router,
      store,
      created() {
        this.$store.dispatch('fetchServices')
        this.$store.dispatch('fetchClients')
      },
      render: h => h(App)
    }).$mount('#app')
    unsubscribe()
})

