import Vue from 'vue'
import VueRouter from 'vue-router'
import AuthGuard from './authGuard'

Vue.use(VueRouter)

const routes = [
  {
    path: '/', 
    name: 'Home', 
    component: () => import ('@/components/Home'),
    beforeEnter: AuthGuard
  },
  {
    path: '/clients',
    name: 'Clients',
    component: () => import ('@/components/Clients'),
    beforeEnter: AuthGuard
  },
  {
    path: '/services',
    name: 'Services',
    component: () => import ('@/components/Services'),
    beforeEnter: AuthGuard
  },
  {
    path: '/finances',
    name: 'Finances',
    component: () => import ('@/components/Finances'),
    beforeEnter: AuthGuard
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import ('@/components/Login')
  },
  {
    path: '/404',
    name: 'NotFoundPage',
    component: () => import ('@/components/NotFoundPage'),
    beforeEnter: AuthGuard
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
