<template>
<v-app>
  <v-navigation-drawer 
    app  
    v-model="drawer"
  >
      <template v-slot:prepend>
        <v-list>
          <v-list-item>
            <v-list-item-avatar>
              <v-img src="https://randomuser.me/api/portraits/men/85.jpg"></v-img>
            </v-list-item-avatar>
          </v-list-item>
          <v-list-item two-line>
            <v-list-item-content v-if="user">
              <v-list-item-title class="title">{{user.uid}}</v-list-item-title>
              <v-list-item-subtitle>{{user.email}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </template>

      <v-divider></v-divider>

      <v-list nav>
        <v-list-item v-for="e in links" :key="e.name"
          link
          :to="{path: e.route}"
        >
          <v-list-item-icon>
            <v-icon>{{e.icon}}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{e.name}}</v-list-item-title>
          </v-list-item-content>
          
        </v-list-item>
      </v-list>

      <template v-slot:append>
        <div class="pa-2">
         <v-switch @change="switchTheme($event)" color="indigo" label="Ночной режим" v-model="ui.darkTheme"></v-switch>
        </div>
      </template>
  </v-navigation-drawer>

  <v-app-bar app>
    <v-app-bar-nav-icon @click.stop="drawer = !drawer" :disabled="!user" class="hidden-sm-and-up"></v-app-bar-nav-icon>
    <v-toolbar-title>lk.ezner.com</v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn text @click="onLogOut" v-if="user">
      <v-icon left>mdi-exit-to-app</v-icon>
      Выход
    </v-btn>
  </v-app-bar>

  <!-- Sizes your content based upon application components -->
  <v-content>
    
    <!-- Provides the application the proper gutter -->
    <v-container fluid>

      <!-- If using vue-router -->
      <router-view></router-view>
    </v-container>
  </v-content>

    <template v-if="error">
      <v-snackbar
      :timeout="5000"
      :multi-line="true"
      color="error"
      @input="closeError"
      :value="loading"
    >
      {{error}}
      <v-btn text dark @click.native="closeError">Close</v-btn>
    </v-snackbar>
    </template>

  <v-footer app>
    <v-spacer></v-spacer>
    <v-badge color="error" class="mx-2">
      <template v-slot:badge>!</template>
      Alpha 0.1
    </v-badge>
  </v-footer>
</v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    drawer: false,
    links: [
      {name: 'Главная', icon: 'mdi-view-dashboard', route: '/'},
      {name: 'Услуги', icon: 'mdi-finance', route: '/services'},
      {name: 'Клиенты', icon: 'mdi-account', route: '/clients'},
      {name: 'Финансы', icon: 'mdi-currency-rub', route: '/finances'},
    ]
  }),
  methods: {
    switchTheme (event) {
      this.$store.dispatch('switchTheme', event)
      this.$vuetify.theme.dark = this.ui.darkTheme
    },
    closeError () {
      this.$store.dispatch('clearError')
    },
    onLogOut () {
      this.$store.dispatch('logoutUser')
      this.$store.dispatch('setDrawer', false)
      this.$router.push('/login')
    }
  },
  computed: {
    ui () {
      // eslint-disable-next-line
      this.drawer = this.$store.getters.getAllInfo.drawer
      return this.$store.getters.getAllInfo
    },
    user () {
      return this.$store.getters.getUserInfo
    },
    error () {
      return this.$store.getters.error
    },
    loading () {
      return this.$store.getters.loading
    },
  },
  beforeCreate () {
    if (window.innerWidth < 700) {
      this.$store.dispatch('setDrawer', false) 
    } else if (this.user !== null) {
      this.$store.dispatch('setDrawer', true) 
    } else {
      this.$store.dispatch('setDrawer', false) 
    }
  },
};
</script>

<style>
  * {
    text-decoration: none;
  }
</style>
